main {
    width: 90%;
    margin: 0 auto;
    color: #747474;
    font-family: sans-serif;
    line-height: 1.5;
    font-size: 16px;
    padding: 3rem 1rem;
    
    @media (min-width: 1280px) {
        width: 1200px;
        font-size: 18px;
        padding-top: 3rem;
        padding-bottom: 3rem;
    }

}

.title {
    text-align: center;
    color: black;
    margin-bottom: 2rem;
}

main > ol {
    &:first-of-type {
        padding-left: 0;
    }

    ol {
        padding-left: 1rem;
    }

    li {
        margin-bottom: 1rem;
        
        li:before {
            padding-left: 1rem
        }
    }
    
}

ol { 
    counter-reset: item;

    li { 
        display: block; 

        &:before { 
            content: counters(item, ".") " "; 
            counter-increment: item;
        }
    }
}
